/*general*/
/*colors*/
.header {
  background-image: url("../img/main-background@1x.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: -27px; }

.header__inner {
  max-width: 1200px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 13px 10px 0 10px; }

.header__logo {
  padding: 0 20px 0 0; }

.header__info p {
  font-style: italic;
  font-weight: 800;
  font-size: 23px;
  line-height: 28px;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;
  padding: 0; }

.header__info {
  font-style: normal;
  font-weight: normal;
  font-size: 8px;
  line-height: 10px;
  color: #ffffff; }

.main-nav {
  flex-grow: 1;
  padding: 9px 112px 0 85px; }

.main-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.main-nav__link {
  text-decoration: none;
  color: #ffffff;
  cursor: pointer; }

.general {
  max-width: 1200px;
  margin: 0 auto; }

.general__inner {
  padding: 184px 0 144px 10px;
  max-width: 600px; }

.general__slogan {
  max-width: 495px; }

.general__inner h1 {
  font-style: normal;
  font-weight: 800;
  font-size: 45px;
  line-height: 55px;
  color: #ffffff;
  margin: 0; }

.general__inner p {
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 148.6%;
  padding: 10px 0 10px;
  color: #ffffff;
  margin: 0; }

.button {
  background-color: #0266c1;
  color: #ffffff;
  border: none;
  cursor: pointer; }

.button--info {
  padding: 20px 38px 20px;
  width: 246px; }
